// Slick slider
//---------------------------------------------------------------
.slick-slider {
	position: relative;
	display: block;
	-webkit-touch-callout: none;
	user-select: none;
	-ms-touch-action: pan-y;
	touch-action: pan-y;
	-webkit-tap-highlight-color: transparent;
}
.slick-list {
	position: relative;
	overflow: hidden;
	display: block;
	margin: 0;
	padding: 0;
	&:focus {
		outline: none;
	}
	&.dragging {
		cursor: pointer;
	}
}
.slick-slider .slick-track,
.slick-slider .slick-list {
	transform: translate3d(0, 0, 0);
	height: 100%;
}

.slick-track {
	position: relative;
	left: 0;
	top: 0;
	display: block;

	&::before,
	&::after {
		content: '';
		display: table;
	}

	&::after {
		clear: both;
	}

	.slick-loading & {
		visibility: hidden;
	}
}
.slick-slide {
	float: left;
	height: 100%;
	min-height: 1px;
	outline: none;
	display: none;
	img {
		max-width: 100%;
		display: block;
	}
	&.slick-loading img {
		display: none;
	}
	&.dragging img {
		pointer-events: none;
	}
	.slick-initialized & {
		display: block;
	}
	.slick-loading & {
		visibility: hidden;
	}
	.slick-vertical & {
		display: block;
		height: auto;
		border: 1px solid transparent;
	}
	@include breakpoint(mobile) {
		img {
			width: 100%;
		}
	}
}
.slick-arrow {
	position: fixed;
	top: 50%;
	margin-top: -19px;
	background-size: 100% 100%;
	background-repeat: no-repeat;
	z-index: 260;
	display: block;
	cursor: pointer;
	.icon {
		font-size: 40px;
	}
	&.slick-prev {
		left: 18px;
	}
	&.slick-next {
		right: 18px;
	}
	&.slick-hidden {
		display: none;
	}
	&.slick-disabled {
		opacity: 0.5;
		cursor: default;
	}
	@include breakpoint(phone) {
		.icon {
			font-size: 30px;
		}
		&.slick-prev {
			left: 10px;
		}
		&.slick-next {
			right: 10px;
		}
	}
}